import React, { useState } from 'react';
import Grid from '@material-ui/core/Grid';
import DeleteIcon from '@material-ui/icons/Delete';


export default function Datatab() {
    const [val, setval]= useState({
            name: '',
            gender: '',
            age: '',
            city: '',
            num: ''
    });

    const [tabrow, settabrow]= useState([]);
    const [row, setrow] = useState();

    const valChange= (e) =>{
        setval((prevVal)=>{
            return { ...prevVal,
                [e.target.name]: e.target.value
            }
        })
    }

    const submitVal= (e) =>{
        // e.preventDefault();
        if(!val){

        }
        // else if(  ){

        // }
        else{
            const newval= { id: new Date().getTime().toString() , val};
            settabrow((prevtabrow)=>{
                
                return [ ...prevtabrow, newval]
            })
        }

        setval({
                name: '',
                gender: '',
                age: '',
                city: '',
                num: ''
            })
        
    }

    const delrow= (id) => {
        settabrow((prevtabrow)=>{
            return (
                prevtabrow.filter((elem)=>{
                    return elem.id != id
                })
            )
        })
    }

    const editrow= (id) => {
        settabrow((prevtabrow)=>{
            return (
                prevtabrow.filter((elem)=>{
                    return elem.id != id
                })
            )
        })
    }
    
    return (
        <div className='main text-center '>
            <h1 className='head py-3'>Datatable</h1>
            <Grid container spacing={1} className='contain m-auto mt-5 ps-5 pb-4'>
                <Grid md={5}>
                    <form action="" method="" className='form mx-auto'>

                        <label htmlFor="text" className='input-group'>Name</label>
                        <input type="text" name="name" id="text" className='form-control' value={val.name} onChange={valChange}/>
                        <label htmlFor="text2" className='input-group  mt-2'>Gender</label>
                        <input type="text" name="gender" id="text2" className='form-control' value={val.gender}  onChange={valChange}/>
                        <label htmlFor="text3" className='input-group  mt-2'>Age</label>
                        <input type="text" name="age" id="text3" className='form-control' value={val.age}  onChange={valChange}/>
                        <label htmlFor="text4" className='input-group  mt-2'>City</label>
                        <input type="text" name="city" id="text4" className='form-control' value={val.city}  onChange={valChange}/>
                        <label htmlFor="text5" className='input-group  mt-2'>Number</label>
                        <input type="text" name="num" id="text5" className='form-control' value={val.num} onChange={valChange}/>
                        <button type="button" onClick={submitVal} className='btn btn-success mt-3'>Save</button>

                    </form>
                </Grid>
                <Grid md={5}>
                    <table className="table table-striped bg-light">
                        <thead>
                            <tr>
                            <th scope="col">#</th>
                            <th scope="col">Name</th>
                            <th scope="col">Gender</th>
                            <th scope="col">Age</th>
                            <th scope="col">City</th>
                            <th scope="col">Number</th>
                            </tr>
                        </thead>
                        <tbody>
                        
                        {
                            tabrow.map((elem, ind)=>{
                            return (
                                <tr>
                                    <th scope="row" key={elem.id} className='d-flex justify-content-between align-items-center'>
                                        <DeleteIcon className='text-danger' onClick={()=> delrow(elem.id)}/>
                                        
                                        {ind}
                                    </th>
                                    <td>{elem.val.name}</td>
                                    <td>{elem.val.gender}</td>
                                    <td>{elem.val.age}</td>
                                    <td>{elem.val.city}</td>
                                    <td >{elem.val.num} </td>
                                </tr>
                            )
                            })
                        }
                            
                        </tbody>
                    </table>
                </Grid>
                
            </Grid>
        </div>
        );
    }



